.container {
  max-width: 1400px;
  // min-width: 320px;
  margin: 0 auto;
  padding: 0 16px;

  @include onTablet {
    padding: 0 44px;
  }
  @include onDesktop {
    padding: 0 44px;
  }
  @include onDesktopXL {
    padding: 0 44px;
  }

  &__header {
    padding: 20px;

    @include onDesktop {
      // max-width: 1200px;
      padding: 32px 36px;
    }
    @include onDesktopXL {
      padding: 32px 36px;
    }
  }

  &__product {
    margin-top: 124px;

    @include onTablet {
      margin-top: 248px;
    }
    @include onDesktop {
      margin-top: 0px;
    }
    @include onDesktopXL {
      margin-top: 0;
    }
  }
}
